<template>
  <div>
    <el-drawer
      title="Edição da Conta"
      :visible.sync="show"
      direction="rtl"
      :before-close="handleClose"
      size="40%"
    >
      <div v-loading="this.loading" style="padding: 0px 20px; box-sizing: border-box;">
        <ul id="DataModuleUpdate">
          <li v-for="field in dataModule.fields" :key="field.field_id">
            <span class="label">{{ field.field_label }}</span>
            <span class="field">
              <el-input v-if="textField()" v-model="dataModule.form[field.field_table_name]"></el-input>

              <el-input
                v-if="textField(field.type_field)"
                v-model="dataModule.form[field.field_table_name]"
              ></el-input>

              <el-switch
                v-if="switchField(field.type_field)"
                v-model="dataModule.form[field.field_table_name]"
              ></el-switch>
              <el-date-picker
                v-if="dateField(field.type_field)"
                v-model="dataModule.form[field.field_table_name]"
                type="date"
                format="dd/MM/yyyy"
              ></el-date-picker>
              <el-select
                v-model="dataModule.form[field.field_table_name]"
                width="100%"
                v-if="selectField(field.type_field)"
                clearable
                placeholder="Selecione"
              >
                <el-option
                  v-for="item in dataModule.fieldsOptions[field.field_table_name]"
                  :key="item.dropdown_option_value"
                  :label="item.dropdown_option_value"
                  :value="item.dropdown_option_value"
                ></el-option>
              </el-select>
            </span>
          </li>
        </ul>
        <el-button @click="saveData()" type="primary">Atualizar Dados</el-button>
      </div>
    </el-drawer>
  </div>
</template>

<script>
export default {
  name: "AccountUpdate",
  props: {
    show: {
      default: false,
    },
  },
  data() {
    return {
      dataID: 0,
      loading: false,
      dataModule: {
        originalData: [],
        fields: [],
        form: {},
        fieldsOptions: {},
      },
    };
  },
  methods: {
    textField(typeField) {
      if (typeField === "text") {
        return true;
      } else {
        return false;
      }
    },
    switchField(typeField) {
      if (typeField === "switch") {
        return true;
      } else {
        return false;
      }
    },
    dateField(typeField) {
      if (typeField === "date") {
        return true;
      } else {
        return false;
      }
    },
    selectField(typeField) {
      if (typeField === "dropdown") {
        return true;
      } else {
        return false;
      }
    },
    saveData() {
      let fields = this.dataModule.fields;

      var is_valid = false;
      fields.forEach((f) => {
        if (
          this.dataModule.form[f.field_table_name] !=
          this.dataModule.originalData[f.field_table_name]
        ) {
          is_valid = true;
          return false;
        }
      });

      if (is_valid == false) {
        this.dataModule.form = {};
        this.$emit("done");
      } else {
        this.XHRSaveDataModule();
      }
    },
    XHRSaveDataModule() {
      this.loading = true;
      var dataRequest = this.dataModule.form;

      this.$http
        .put("/module/accounts/" + this.dataID, dataRequest)
        .then((res) => {
          this.loading = false;
          this.$notify.success({
            title: "Sucesso",
          });
          this.$emit("done");
          res;
        })
        .catch((err) => {
          this.loading = false;
          err;
          this.$notify.error({
            title: "Erro na soliciatação",
          });
        });
    },
    handleClose(done) {
      this.$confirm("Deseja sair da tela de atualização?")
        .then((_) => {
          _;
          this.$emit("done");
          done();
        })
        .catch((_) => {
          _;
        });
    },
    getOldDataFromDataModule(id) {
      this.$http.get("/module/accounts/" + id).then((res) => {
        this.dataModule.originalData = res.data;
      });
    },
    getFieldsFromDataModule() {
      this.$http.get("/fields/accounts").then((resFields) => {
        this.dataModule.fields = resFields.data;
        resFields.data.forEach((element) => {
          this.$set(
            this.dataModule.form,
            element.field_table_name,
            this.dataModule.originalData[element.field_table_name]
          );
        });
      });
    },
    mountFormUpdate() {
      this.$http.get("/module/accounts/" + this.dataID).then((res) => {
        this.dataModule.originalData = res.data;

        this.$http.get("/fields/accounts").then((resFields) => {
          this.dataModule.fields = resFields.data;
          resFields.data.forEach((element) => {
            if (element.type_field === "dropdown") {
              this.$http
                .get("/fields/dropdown-values/" + element.field_id)
                .then((df) => {
                  this.$set(
                    this.dataModule.fieldsOptions,
                    element.field_table_name,
                    df.data
                  );
                });
            }

            this.$set(
              this.dataModule.form,
              element.field_table_name,
              this.dataModule.originalData[element.field_table_name]
            );
          });
        });
      });
    },
  },
  watch: {
    show: function (newState, oldState) {
      newState;
      oldState;
      this.dataID = this.$route.params.id;

      this.mountFormUpdate();
    },
  },
};
</script>

<style lang="scss">
#DataModuleUpdate {
  margin: 0px;
  padding: 0px;
  li {
    list-style: none;
    padding-bottom: 20px;

    span {
      display: inline-block;

      &.label {
        width: 40%;
        font-weight: 600;
      }
    }
  }
}
</style>